(function() {
    'use strict';

    angular
        .module('fanscount')
        .service('UserService', [
            '$q',
            '$http',
            'apiUrl',
            UserService]);

    function UserService($q, $http, apiUrl) {
        this.$q = $q;
        this.$http = $http;
        this.apiUrl = apiUrl;
        
        return this;
    }

    UserService.prototype.signUp = function (userData) {
        var deferred = this.$q.defer();
        this.$http({
            url: this.apiUrl + '/users',
            method: 'POST',
            data: userData
        }).success(function(data) {
            deferred.resolve(data);
        }).error(function(err, status) {
            deferred.reject(err, status);
        });
        return deferred.promise;
    };

    UserService.prototype.update = function (userData) {
        console.log(userData);
        var deferred = this.$q.defer();
        this.$http({
            url: this.apiUrl + '/users/' + userData._id,
            method: 'PUT',
            data: userData
        }).success(function(data) {
            deferred.resolve(data);
        }).error(function(err, status) {
            deferred.reject(err, status);
        });
        return deferred.promise;
    };
})();

(function() {
    'use strict';

    angular
        .module('fanscount')
        .service('LeagueService', [
            '$q',
            '$http',
            'apiUrl',
            LeagueService]);

    function LeagueService($q, $http, apiUrl) {
        this.$q = $q;
        this.$http = $http;
        this.apiUrl = apiUrl;

        return this;
    }

    LeagueService.prototype.getByCountry= function (countryId) {
        var deferred = this.$q.defer();
        this.$http({
            url: this.apiUrl + '/leagues/country/' + countryId,
            method: 'GET'
        }).success(function(data) {
            deferred.resolve(data);
        }).error(function(err, status) {
            deferred.reject(err, status);
        });
        return deferred.promise;
    };
})();

(function() {
    'use strict';

    angular
        .module('fanscount')
        .service('CountryService', [
            '$q',
            '$http',
            'apiUrl',
            CountryService]);

    function CountryService($q, $http, apiUrl) {
        this.$q = $q;
        this.$http = $http;
        this.apiUrl = apiUrl;

        return this;
    }

    CountryService.prototype.getAll= function () {
        var deferred = this.$q.defer();
        this.$http({
            url: this.apiUrl + '/countries',
            method: 'GET'
        }).success(function(data) {
            deferred.resolve(data);
        }).error(function(err, status) {
            deferred.reject(err, status);
        });
        return deferred.promise;
    };
})();

(function() {
    'use strict';

    angular
        .module('fanscount')
        .controller('LeagueController', [
            '$scope',
            '$state',
            '$stateParams',
            'LeagueService',
            LeagueController]);

    /** @ngInject */
    function LeagueController($scope, $state, $stateParams, LeagueService) {
        this.$ = $scope;
        this.$state = $state;
        this.$stateParams = $stateParams;
        this.leagueService = LeagueService;
        this.init();

        return this;
    }

    LeagueController.prototype.init = function () {
        this.leagueService.getByCountry(this.$stateParams.country)
            .then(function(leagues) {
                this.$.leagues = leagues;
            }.bind(this))
            .catch(function(err) {
                console.log(err); //todo change this to error handler
            })
    };
})();
(function() {
    'use strict';

    angular
        .module('fanscount')
        .controller('HomeController', [
            '$scope',
            '$state',
            'CountryService',
            HomeController]);

    /** @ngInject */
    function HomeController($scope, $state, CountryService) {
        this.$ = $scope;
        this.$state = $state;
        this.countryService = CountryService;
        this.init();

        return this;
    }

    HomeController.prototype.init = function () {
        this.$.name = 'boyov & sirun';

        this.countryService.getAll()
            .then(function(countries) {
                this.$.countries = countries;
            }.bind(this))
            .catch(function(err) {
                console.log(err); //todo change this to error handler
            })
    };

    HomeController.prototype.goToLeagues = function(country) {
        this.$state.go('leagues.country', {country: country});
    }

})();
